.footer{position:fixed;bottom:0px;left:0px;background-color:#8a0c00;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:row;flex-direction:row;width:100vw;height:6vh;min-height:70px}.footer .footer-logo-container{max-width:1024px;width:85%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-flex-direction:row;flex-direction:row}.footer .footer-logo-container a{padding:1%}.footer .footer-logo-container a img{height:100%}@media only screen and (max-width: 426px){.footer .footer-logo-container a{display:block;height:auto;width:auto}.footer .footer-logo-container a img{display:block;max-height:100%;max-width:100%;height:auto;width:auto}}
.header{position:-webkit-sticky;position:sticky;top:0;background-color:#8a0c00;height:7vh}.nav-bar{background-color:#8a0c00;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;height:7vh}.nav-link{color:#fff;text-decoration:none;padding:0 3%;font-weight:600;-webkit-align-self:center;align-self:center}.nav-link:hover{color:#fa743c}.nav-link:active{margin-top:.2%;color:#fa743c}.nav-link .selected{color:#fa743c}.nav-link-selected{color:#fa743c;text-decoration:none;padding:0 3%;font-weight:600;-webkit-align-self:center;align-self:center}.nav-link-selected:hover{color:#fa743c}.nav-link-selected:active{margin-top:.2%;color:#fa743c}.nav-link-selected .selected{color:#fa743c}.current-page-mobile{color:#fff;font-size:32px;display:none;margin-left:6px}.burger-button{display:none;cursor:pointer;position:absolute;margin:6px;width:30px;right:20px;z-index:3;background-color:#8a0c00}.opened-bar-1,.opened-bar-2,.opened-bar-3{width:35px;height:5px;background-color:#fff;margin:6px 0;transition:.4s}.closed-bar-1{width:35px;height:5px;background-color:#fff;margin:6px 0;transition:.4s;-webkit-transform:rotate(-45deg) translate(-9px, 6px);transform:rotate(-45deg) translate(-9px, 6px)}.closed-bar-2{width:35px;height:5px;background-color:#fff;margin:6px 0;transition:.4s;opacity:0}.closed-bar-3{width:35px;height:5px;background-color:#fff;margin:6px 0;transition:.4s;-webkit-transform:rotate(45deg) translate(-8px, -8px);transform:rotate(45deg) translate(-8px, -8px)}@media(max-width: 445px){.burger-button,.current-page-mobile{display:inline-block}.header{height:50px}.nav-bar-mobile{width:100%;position:absolute;z-index:3;background-color:#8a0c00}.nav-bar-mobile .nav-link,.nav-bar-mobile .nav-link-selected{display:block;padding:12px}.nav-bar-mobile .nav-link:hover,.nav-bar-mobile .nav-link-selected:hover{color:#fa743c}.nav-bar-mobile .nav-link:active,.nav-bar-mobile .nav-link-selected:active{margin-top:.2%;color:#fa743c}.nav-bar,.current-page{display:none}}
