.card{margin:20px;width:100%;margin-left:auto;margin-right:auto;box-shadow:10px 10px #8a0c00;background-color:#efefef;width:100%;display:-webkit-flex;display:flex;text-decoration:none}.card-image-right{-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.card-image{object-fit:cover;height:300px;width:300px;float:right;border:10px solid #efefef}.card-text-block{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:left;align-items:left;-webkit-justify-content:center;justify-content:center}.card-title{background-color:#c1440d;font-size:32px;color:#fff;margin:-10px 0;padding-left:10px;padding-bottom:10px}.card-body{background-color:#fa743c;font-size:16px;color:#000;margin:0 20px;padding-left:10px;padding-bottom:15px}@media(max-width: 600px){.card{box-shadow:0px 0px;margin-left:auto;margin-right:auto;width:100%;-webkit-flex-direction:column;flex-direction:column;padding:0px}.card-image{height:393px;width:393px;margin:16px;margin-bottom:0px;border:0px}.card-title{font-size:24px}.card-body{font-size:14px}}@media(max-width: 475px){.card{box-shadow:0px 0px;margin-left:auto;margin-right:auto;width:100%;-webkit-flex-direction:column;flex-direction:column;padding:0px}.card-image{height:300px;width:300px;margin:10px;margin-bottom:0px;border:0px}.card-title{font-size:18px}.card-body{font-size:10px}}
.thumbnail-gallery{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-flex-wrap:wrap;flex-wrap:wrap}.thumbnail-gallery .small-photo-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:16vw;height:auto;margin:1vw}.thumbnail-gallery .small-photo-container .small-photo{width:100%;height:auto;max-height:100%}.thumbnail-gallery .small-photo-container .small-photo-selected{border:.5vw solid #fa743c;width:92%;height:auto;max-height:100%}@media only screen and (max-width: 426px){.thumbnail-gallery{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-flex-wrap:wrap;flex-wrap:wrap}.thumbnail-gallery .small-photo-container{width:90vw;height:auto;margin:1vw}.thumbnail-gallery .small-photo-container .small-photo{width:100%;height:auto;max-height:100%}.thumbnail-gallery .small-photo-container .small-photo-selected{border:1vw solid #fa743c;width:88vw;height:auto;max-height:100%}}

